<template >
  <article class="ListVue">
    <div v-for="(item, index) in ListData" :key= "(index)">
      <h5 @click="$router.push('/detail/'+index+'')">{{ ListData[index].title }}</h5>
      <p>{{ ListData[index].content }}</p>
    </div>
  </article>
</template>

<!-- @click="$router.go(-1)은 뒤로가기"  router는 페이지 이동관련 기능-->
<!-- @click="$route.fullPath는 전체 경로 불러오기" *** route는 현재 경로 관련 기능-->
<!-- Vue Router Essentials에 가면 확인 가능 -->
<script>

export default {
  name: 'detail',
  props: {
    ListData: Array,
  }

}
</script>

<style>
.ListVue {
  display: flex;
  flex-direction: column;
  gap:30px;
  margin-top: 15px;
}

.ListVue h5 {
  font-weight: bold;
}

</style>